<template>
  <div>
    <div v-if="currentPage==0">
      <h2 class='title'>此目录下为{{pageTitle}}相关笔记</h2>
      <div class='title-index' v-for="item in articles" @click="currentPage = item.index" :key="item.index">
        {{item.name}}
      </div>
      <div class='nocontent' v-if="articles.length==0">暂无笔记收录</div>
    </div>
    <div class='markdowns' v-else>
      <!-- <div class='article' v-html='articles[currentPage-1].article'/> -->
      <!-- <mavon-editor defaultOpen="preview" :editable="false" :ishljs='true' :value="articles[currentPage-1].article"/> -->
      <markdown-it-vue-light class="article" :content="articles[currentPage-1].article" :options="option"/>
    </div>
    
  </div>
</template>

<script>
import MarkdownItVueLight from 'markdown-it-vue/dist/markdown-it-vue-light.umd.min.js'
import 'markdown-it-vue/dist/markdown-it-vue-light.css'
export default {
  props:{
    pageTitle:{
      default:"",
    },
    articles:{
      default(){
        return []
      }
    },
    initPage:{
      default:0
    }
  },
  data(){
    return {
      currentPage:0,
      option:{
        markdownIt: {
          html:true,
          linkify: true,
        },
      }
    }
  },
  components:{
    MarkdownItVueLight
  },
  mounted(){
    this.currentPage=this.initPage;
  }
}
</script>

<style scoped>
  .title{
    padding: 0.2rem ;
    text-align: left;
  }

  .title-index{
    padding: 0.4rem 1rem;
    text-align: center;
    background-color: orange;
    margin: 0.1rem;
    font-size: 0.32rem;
    color: white;
  }

  .markdowns{
    padding-top: 0.5rem;
    overflow-x: auto;
    line-height: 0.4rem;
    font-size: 0.3rem;
  }

  .nocontent{
    padding-top: 0.5rem;
    text-align: center;
    font-size: 0.36rem;
  }
</style>