<template>
  <div class='detail-page'>
    <div class='markdown-container'>
      <PageModule :pageTitle='pageTitle' :articles='articleList' :initPage='page'/>
    </div>
    <BackTop/>
  </div>
</template>

<script>
import ArticleData from './ArticleData'

import BackTop from 'components/common/backTop/BackTop'
import PageModule from './childViews/PageModule'
export default {
  name:"Detail",
  data(){
    return {
      type:"Vue",
      page:0,
      pageTitle:"",
      articleList:"",
    }
  },
  components:{
    BackTop,
    PageModule
  },
  mounted(){
    this.type=this.$route.params.type;
    this.page=this.$route.params.page;
    this.getPageInfo();
  },
  methods:{
    getPageInfo(){
      for(let i=0;i<ArticleData.length;i++){
        if(ArticleData[i].type==this.type){
          this.articleList=ArticleData[i].articles;
          this.pageTitle=ArticleData[i].title;
        }
      }

    }
  }
}
</script>

<style scoped>
  .detail-page{
    font-size: 0.28rem;
    font-weight: bolder;
  }

  .markdown-container{
    font-family: 'monospace','monospace';
    background-color: #f6f6f6;
    color: black;
    padding: 1rem 0.5rem 1rem 0.5rem;
  }


</style>
